nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: #dac292;
}

h3.a {
  float: right;
}

#dropdown-basic-button {
  background-color: #dac292;
  color: black;
  border: none;
  width: 15%;
  margin: 10px;
}

.container-style {
  margin: 15px;
  border-style: solid;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  border-color: #7a653b;
  background-color: #dac292;
}

.container-style .info {
  margin: 15px;
}

.container-style img {
  margin: 8px;
  float: right;
  width: 50%;
  border-radius: 50%;
}

.container-style:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

ul {
  float: right;
}
li {
  color: blue;
  text-decoration: underline;
  list-style-type: none;
}

.construction {
