nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: #dac292;
}

h3.a {
  float: right;
}

#dropdown-basic-button {
  background-color: #dac292;
  color: black;
  border: none;
  width: 15%;
  margin: 10px;
}

.container-style {
  margin: 15px;
  border-style: solid;
  transition: 0.2s;
  border-color: #7a653b;
  background-color: #dac292;
}

.container-style .info {
  margin: 15px;
}

.container-style img {
  margin: 8px;
  float: right;
  width: 50%;
  border-radius: 50%;
}

.container-style:hover {
  transform: scale(1.05);
}

ul {
  float: right;
}
li {
  color: blue;
  text-decoration: underline;
  list-style-type: none;
}

.construction {
  min-height: 100%;
  min-width: 1024px;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0.5;
}

h1.a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-weight: bold;
}

.homepage-style-main {
  position: relative;
  left: 45%;
  max-width: 100vh;
  max-width: 100vh;
  min-width: 25vh;
  min-height: 80vh;
  border: solid;
}

.online-container {
  margin-left: 40px;
  margin-right: 40px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.content {
  max-height: 75vh;
  overflow: auto;
}
